import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import "./awards.css"
import ThankYouLetter from "../components/thankYouLetter"
import Certificate from "../components/certificate"

const awards = () => {
  return (
    <Layout page="awards">
      <Seo title="Awards" description="It is our pleasure to help." />
      <section className="title-div-white">
        <strong className="title-awards">IT IS OUR PLEASURE TO HELP</strong>
      </section>
      <section className="section-awards">
        <div className="widthLimit-awards">
          <div className="columns">
            <div className="column is-half has-text-centered">
              <div className="container">
                <ThankYouLetter className="images" />
                <div className="titleOfSlider">THANK YOU LETTERS</div>
              </div>
            </div>
            <div className="column has-text-centered">
              <div className="container">
                <Certificate className="images" />
                <div className="titleOfSlider">CERTIFICATES</div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="title-div-white" />
    </Layout>
  )
}

export default awards
