import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Carousel, { CarouselItem } from "./Carousel"
import { GatsbyImage } from "gatsby-plugin-image"

const Certificate = ({ ...rest }) => {
  const data = useStaticQuery(graphql`{
    allStrapiAwards {
      nodes {
        certificates {
          localFile {
            childImageSharp {
              gatsbyImageData(layout: CONSTRAINED, formats: [AUTO, WEBP, AVIF])
            }
            name
          }
        }
      }
    }
  }`);

  return <Carousel>{data.allStrapiAwards.nodes.map(nodes => (
    nodes.certificates.map(result => (
      <CarouselItem><GatsbyImage image={result.localFile.childImageSharp.gatsbyImageData} {...rest} alt={result.localFile.name} /></CarouselItem>
    ))
  ))}</Carousel>
};

export default Certificate;